  methods: {
    getDateInfo(dateString) {
      const date = new Date(dateString);

      if (isNaN(date.getTime())) {
        throw new Error('Invalid date string');
      }

      const weekDays = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'];

      const dayOfWeek = date.getDay();

      const month = date.getMonth() + 1;

      const day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();

      const formattedDate = `${month}/${day}`;

      return {
        dayOfWeek: weekDays[dayOfWeek],
        formattedDate: formattedDate
      };
    },

    calculateDaysDifference(dateStrings) {
      if (dateStrings.length < 2) {
        throw new Error('Input array must contain at least two date strings');
      }
      dateStrings.sort((a, b) => new Date(a) - new Date(b));

      let totalDays = 0;
      for (let i = 1; i < dateStrings.length; i++) {
        const date1 = new Date(dateStrings[i - 1]);
        const date2 = new Date(dateStrings[i]);

        const diffTime = date2 - date1;
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

        totalDays += diffDays;
      }

      return totalDays;
    },
