    addDaysToDateRange(dateRange, daysToAdd) {
      // 转换日期字符串为Date对象数组  
      const startDateObjects = dateRange.map(dateString => new Date(dateString));

      // 创建一个新的数组来存储调整后的日期  
      const adjustedDateObjects = startDateObjects.map(startDate => {
        // 创建一个副本以避免修改原始Date对象  
        const adjustedDate = new Date(startDate);

        // 增加或减少天数  
        adjustedDate.setDate(adjustedDate.getDate() + daysToAdd);

        // 转换回日期字符串  
        return adjustedDate.toISOString().split('T')[0];
      });

      // 返回新的日期区间数组  
      return adjustedDateObjects;
